@import url('https://fonts.googleapis.com/css2?family=Diphylleia&display=swap');
.App {
  text-align: center;
  background-color: rgb(236, 229, 229);
  font-family: 'Diphylleia', serif;
  height: auto;
  width: auto;
}

.main-bg{
  height: 300px;
  background-image: url('/public/arsenal.jpg');
  background-size: cover;
  background-position: center;
  margin-bottom: 20px;
}

/* .col-sm-4{
} */

.PlayerPrice{
  width: 600px;
  background-color: black;
  margin: auto;
  margin-top: 10px;
  margin-bottom: 20px;
  border-top: 3px solid red;
  color: white;
}

.PlayerPrice>p{
  padding-top: 9px;
}

.PlayerPrice>div{
  background-color: white;
  color: black;
  height: 30px;
}

.PlayerPrice>table{
  background-color: white;
  color: black;
}

.col-sm-4:hover{
  cursor: pointer;
  background: #ecdada;
}
.signing{
  height: 550px;
}

.signing div{
  padding: 10px;
}

.signing input, textarea{
  width: 500px;
}

.signing textarea{
  height: 200px;
}

.detailPage Button{
  margin: 10px;
}

.PlayerIntro{
  width: 500px;
  margin: auto;
}

.PlayerPic img{
  width: 600px;
  height: 600px;
}

.introBox{
  background-color: white;
}

